/* harmony default export */ __webpack_exports__["a"] = ({Global:{appTitle:'Bildungsgeschichte Schweiz',loading:'Lädt...',open:'Öffnen',more:'mehr anzeigen',from:'Von',until:'Bis',publicationDate:'Publikationsdatum',referencePeriod:'Referenzzeitraum',tipsTricks:'Tipps & Tricks',noscript:'Bitte JavaScript aktivieren um zu suchen.',poweredByText:'powered by Eurospider',eurospiderLink:'https://www.eurospider.com/de/'},Footer:{about:'Das Projekt',projects:'Die Bestände',rights:'Rechtliche Hinweise',copyright:'© 2018, Universität Zürich'},Home:{quickInfo:'Wissensportal zur Geschichte von Bildung und Erziehung'},RecordList:{resetFacetSearch:'Suche ohne Einschränkungen wiederholen'},Records:{foundDocs:'Dokumente gefunden'},SortDropdown:{asc:'Älteste zuerst (Publikationsdatum)',desc:'Neueste zuerst (Publikationsdatum)',relevance:'Relevanz',sortBy:'Sortieren nach'},About:{text:'\n<h2>Das Projekt</h2>\n<h3>Projektziele</h3>\n<p>Das Wissensportal <em>Bildungsgeschichte Schweiz</em> erm\xF6glicht Forschenden, Studierenden, Lehrenden und einer interessierten \xD6ffentlichkeit eine disziplin\xE4r strukturierte sowie mehrsprachige Recherche zu Quellen und Daten der historischen Bildungsforschung f\xFCr die Schweiz. Die unterschiedlichen digitalen Infrastrukturen sind \xFCber eine Suchmaske auffindbar.</p>\n<p>Forschenden wird eine einfache M\xF6glichkeit zur Ver\xF6ffentlichung bildungshistorischer Forschungsdaten im Sinne des Forschungsdatenmanagements (SNF-Strategie zu Open Research Data) erm\xF6glicht.</p>\n<h3 id="projektorganisation">Projektorganisation</h3>\n<h4 id="disziplin\xE4re-begleitgruppe">Disziplin\xE4re Begleitgruppe</h4>\n<p>Arbeitsgruppe Historische Bildungsforschung (SGBF)</p>\n<h4 id="projektkernteam-technische-entwicklungsphase">Projektkernteam (technische Entwicklungsphase)</h4>\n<p>Christina Rothen; Thomas Ruoss; Lucien Criblez (Universit\xE4t Z\xFCrich)<br />\nProjektleitung\n<p>Peter Sch\xE4uble (<a href="https://www.eurospider.com/de/">Eurospider Information Technology AG)</a><br />\nEntwicklung der Suchmaschine</p>\n<p>Amanda Sauter &amp; Mauro Bieg (2324.ch)<br />\nDesign und Entwicklung des Frontends</p>\n<p>Sergio Maffioletti (S3IT, Universit\xE4t Z\xFCrich)<br />\nBackend und Langzeit-Repository (Koordination DaSCH)</p>\n<p>Carmen Flury, Tamara Lehner-Loosli, Lars Heinzer (Universit\xE4t Z\xFCrich)<br />\nProjektmitarbeitende Bildung in Zahlen</p>\n<p>Bei den Mitarbeitenden des Dipartimento formazione e apprendimento della Scuola universitaria professionale della Svizzera italiana (SUPSI DFA), der Bibliothek f\xFCr Bildungsgeschichtliche Forschung des DIPF (BBF), des Schweizerischen Bundesarchivs, des Staatsarchivs Z\xFCrich, der Zentralbibliothek Z\xFCrich, der ETH-Bibliothek und der Stiftung Pestalozzianum sowie namentlich bei Flavian Imlig, Wolfgang Saalfeld, Lars M\xFCller, Anja Giudici, Giorgia Masoni, Alexandre Fontaine, Karin Manz, Caroline Suter und Simon Allemann bedanken wir uns f\xFCr die vielseitige Unterst\xFCtzung.</p>\n<h3>Kontakt</h3>\n<p><a href="mailto:redaktion@bildungsgeschichte.ch">redaktion@bildungsgeschichte.ch</a></p>\n      '},Projects:{text:'\n<h2 id="die-best\xE4nde">Die Best\xE4nde</h2>\n<p>(Stand 26.06.2018)</p>\n<h3 id="bildung-in-zahlen"><a href="http://www.bildungsgeschichte.uzh.ch">Bildung in Zahlen</a></h3>\n<p>Institut f\xFCr Erziehungswissenschaft, Universit\xE4t Z\xFCrich</p>\n<h3 id="erziehungsdirektoren"><a href="http://www.bildungsgeschichte.uzh.ch">Erziehungsdirektoren</a></h3>\n<p>Institut f\xFCr Erziehungswissenschaft, Universit\xE4t Z\xFCrich</p>\n<h3 id="fondo-gianini-e-rara"><a href="https://www.e-rara.ch/fondo/nav/classification/10916052">Fondo Gianini (e-rara)</a></h3>\n<p>Plattform f\xFCr digitalisierte Drucke aus Schweizer Bibliotheken</p>\n<h3 id="fondo-gianini-supsi"><a href="http://fondo-gianini.supsi.ch/">Fondo Gianini (SUPSI)</a></h3>\n<p>Dipartimento formazione e apprendimento della Scuola universitaria professionale della Svizzera italiana (SUPSI DFA)</p>\n<h3 id="scriptura-paedagogica-online"><a href="http://goobiweb.bbf.dipf.de/viewer/browse/">Scriptura Paedagogica Online</a></h3>\n<p>Bibliothek f\xFCr Bildungsgeschichtliche Forschung Berlin (P\xE4dagogische Nachschlagewerke und P\xE4dagogische Zeitschriften) (8.2018)</p>\n<h3 id="stapfer-enqu\xEAte-1799"><a href="https://opendata.swiss/de/dataset/stapfer-enquete-schule-in-der-schweiz-1799">Stapfer-Enqu\xEAte 1799</a></h3>\n<p><a href="http://www.stapferenquete.ch/projekt">Projektbeschrieb</a></p>\n<p>Schweizerisches Bundesarchiv</p>\n<h3 id="storiascuola"><a href="http://www2.supsi.ch/cms/storiascuola/">Storiascuola</a></h3>\n<p>Dipartimento formazione e apprendimento della Scuola universitaria professionale della Svizzera italiana (SUPSI DFA)</p><h3 id="z\xFCrcher-klassenfotoarchiv"><a href="https://archives-quickaccess.ch/search/1">Z\xFCrcher Klassenfotoarchiv</a></h3>\n<p>Staatsarchiv des Kantons Z\xFCrich</p>\n<h3 id="z\xFCrcher-schulumfrage-177172"><a href="https://www.archives-quickaccess.ch/search/3">Z\xFCrcher Schulumfrage 1771/72</a></h3>\n<p>Staatsarchiv des Kantons Z\xFCrich</p>\n      '},Rights:{text:'\n<h2>Rechtliche Hinweise</h2>\n<h3>Haftungsausschluss</h3>\n<p>Obwohl Bildungsgeschichte Schweiz mit aller Sorgfalt auf die Richtigkeit der ver\xF6ffentlichten Informationen achtet, kann hinsichtlich der inhaltlichen Richtigkeit, Genauigkeit, Aktualit\xE4t, Zuverl\xE4ssigkeit und Vollst\xE4ndigkeit dieser Informationen keine Gew\xE4hrleistung \xFCbernommen werden.</p>\n<p>Haftungsanspr\xFCche gegen Bildungsgeschichte Schweiz wegen Sch\xE4den materieller oder immaterieller Art, welche aus dem Zugriff oder der Nutzung bzw. Nichtnutzung der ver\xF6ffentlichten Informationen, durch Missbrauch der Verbindung oder durch technische St\xF6rungen entstanden sind, werden ausgeschlossen.</p>\n<p>Bildungsgeschichte Schweiz hat fremde, das heisst nicht auf ihren Servern oder in ihrem Einflussbereich liegende Websites, die mit dieser Website \xFCber Verkn\xFCpfung allenfalls verbunden sind, nicht \xFCberpr\xFCft und \xFCbernimmt keine Verantwortung f\xFCr die Inhalte oder die darauf angebotenen Dienstleistungen. Sollten die Inhalte verlinkter Seiten Anlass zur Beanstandung geben, bittet Bildungsgeschichte Schweiz um Mitteilung.</p>\n<h3>Nutzungsbedingungen</h3>\n<p>Bildungsgeschichte Schweiz besitzt keine Urheberrechte an den Inhalten des Wissensportals. Die Rechte liegen in der Regel bei den Bestandseignern.</p>\n<p>Die auf Bildungsgeschichte Schweiz ver\xF6ffentlichten Dokumente stehen fu\u0308r nicht-kommerzielle Zwecke in Lehre und Forschung sowie fu\u0308r die private Nutzung frei zur Verf\xFCgung.</p>\n<p>Das Ver\xF6ffentlichen von Texten und Bildern in Print- und Online-Publikationen ist nur mit vorheriger Genehmigung der Rechteinhaber erlaubt. Die systematische Speicherung des ganzen oder von Teilen des elektronischen Angebots auf anderen Servern bedarf ebenfalls des schriftlichen Einverst\xE4ndnisses der Rechteinhaber.</p>\n      '},Tips:{text:'\n    <h2>Tipps &amp; Tricks</h2>\n    <h3>Suche nach Phrasen</h3>\n    <p>Suche nach zusammengeh\xF6rigen Ausdr\xFCcken:"H\xF6here T\xF6chterschule Z\xFCrich"<br /> (Verwenden sie dazu die sog. Doppelten Kodierungszeichen)</p>\n    <h3>Einschr\xE4nkungen</h3>\n    <p>Die Suche kann eingeschr\xE4nkt werden auf einzelne Bereiche des Dokuments</p>\n    <ul>\n      <li>title:Pestalozzi (Suche nur im Titel des Dokuments)</li>\n      <li>body:Pestalozzi (Suche nur im Volltext des Dokuments)</li>\n    </ul>\n    <p>Die Suche kann eingeschr\xE4nkt werden auf einzelne Metadaten</p>\n    <ul>\n      <li>author:"Johann Heinrich Pestalozzi" (Suche nach Autor) </li>\n      <li>publicationplace:Bern (Suche nach Publikationsort)</li>\n      <li>publicationdate:1831 (Suche nach Publikationsdatum)</li>\n      <li>municipality:Aarau (Suche nach Gemeinde)</li>\n      <li>canton:Z\xFCrich (Suche nach Kanton)</li>\n      <li>nation:Frankreich (Suche nach Nation)</li>\n      <li>actors:Beh\xF6rden (Suche nach Akteuren)</li>\n      <li>texttype:Lehrmittel (Suche nach Texttypen)</li>\n      <li>mediatype:Grafik (Suche nach Medientyp)</li>\n      <li>language:Franz\xF6sisch (Suche nach Sprache)</li>\n      <li>project:Stapfer-Enqu\xEAte (Suche nach Bestand/Projekt)</li>\n      <li>educationssystem:Vorschule (Suche nach Bildungssystematik)</li>\n      <li>subject:Geschlecht (Suche nach Bezugsthema)</li>\n    </ul>\n    <h3>Suchoperatoren</h3>\n    <p>Standardm\xE4ssig werden Suchausdr\xFCcke mit <em>oder</em> verkn\xFCpft, d.h. <br/><em>Kindergarten municipality:Kilchberg</em><br/> sucht nach Dokumenten in denen entweder Kindergarten vorkommt oder die Gemeinde Kilchberg.</p>\n    <p>Wird das Zeichen + verwendet, kommt der nachstehende Begriff zwingend vor. Wird das Zeichen - verwendet, kommt der nachstehende Begriff zwingend nicht vor.<br /> Folgende zwei Beispiele verdeutlichen die genaue Anwendung:<br/> +Kindergarten +Fr\xF6bel (Alle Suchergebnisse\n      enthalten zwingend beide Begriffe Kindergarten und Fr\xF6bel)<br/> +Kindergarten -Fr\xF6bel (Alle Suchergebnisse enthalten Kindergarten ohne den Begriff Fr\xF6bel zu enthalten)<p>\n      <p> Um eine beliebige Anzahl Zeichen am Ende des Suchbegriffs offen zu lassen, kann das Zeichen * verwendet werden. </br>\n      Pestal* (Suchergebnisse sind Pestalozzi, Pestalozzianum, Pestalozzi-Schule, etc.) </p>\n      '}});

