/* harmony default export */ __webpack_exports__["a"] = ({Global:{appTitle:'History of Education Switzerland',loading:'Loading…',open:'Open',more:'show more',from:'From',until:'To',publicationDate:'Publication date',referencePeriod:'Period of reference',tipsTricks:'Tips & Tricks',noscript:'Please enable JavaScript to search.',poweredByText:'powered by Eurospider',eurospiderLink:'https://www.eurospider.com/en/'},Footer:{about:'The Project',projects:'The Inventory',rights:'Legal Notices',copyright:'© 2018, University of Zurich'},Home:{quickInfo:'Knowledge portal on the history of schooling and education'},RecordList:{resetFacetSearch:'Retry the search without restrictions'},Records:{foundDocs:'documents found'},SortDropdown:{asc:'Oldest first (publication date)',desc:'Newest first (publication date)',relevance:'Relevance',sortBy:'Sort by'},About:{text:'\n<h2>The Project</h2>\n<h3>Project Goals</h3>\n<p>The <em>History of Education</em> knowledge portal enables researchers, students, teachers and interested members of the public to carry out discipline-specific and multilingual research on sources and data relating to the history of education research in Switzerland. Various digital sources on the history of education are accessible through a search mask.</p>\n<p>This site offers an easy way for researchers to publish their data in the field of history of education as laid out by the SNF open research data strategy\'s research data management plan.</p>\n<h3 id="project-organisation">Project Organisation</h3>\n<h4 id="subject-specific-experts">Subject specific experts</h4>\n<p>Working Group History of Education (SGBF)</p>\n<h4 id="core-project-team-technical-development-phase">Core project team (technical development phase)</h4>\n<p>Christina Rothen; Thomas Ruoss; Lucien Criblez (University of Zurich)<br />\nProject lead</p>\n<p>Peter Sch\xE4uble (<a href="https://www.eurospider.com/en/">Eurospider Information Technology AG</a>)<br />\nSearch engine development</p>\n<p>Amanda Sauter &amp; Mauro Bieg (2324.ch)<br />\nFrontend design and development</p>\n<p>Sergio Maffioletti (S3IT, University of Zurich)<br />\nBackend and long-term-repository (Coordination DaSCH)</p>\n<p>Carmen Flury, Tamara Lehner-Loosli, Lars Heinzer (University of Zurich)<br />\nProject team Bildung in Zahlen (Education by Numbers)</p>\n<p>We would like to thank the employees of: the Dipartimento formazione e apprendimento della Scuola universitaria professionale della Svizzera italiana (SUPSI DFA), the Bibliothek f\xFCr Bildungsgeschichtliche Forschung des DIPF (BBF) (the DIPF library for research in the history of education), the Swiss federal archives, the Zurich state archives, the Zurich central library (ZB), the ETH library, and the Pestalozzianum foundation. Our thanks also go to Flavian Imlig, Wolfgang Saalfeld, Lars M\xFCller, Anja Giudici, Giorgia Masoni, Alexandre Fontaine, Karin Manz, Caroline Suter and Simon Allemann for their valuable support.</p>\n<h3>Contact</h3>\n<p><a href="mailto:redaktion@bildungsgeschichte.ch">redaktion@bildungsgeschichte.ch</a></p>\n      '},Projects:{text:'\n<h2 id="collections">Collections</h2>\n<p>(Status 26.06.2018)</p>\n<h3 id="bildung-in-zahlen"><a href="http://www.bildungsgeschichte.uzh.ch">Bildung in Zahlen</a></h3>\n<p>Institut f\xFCr Erziehungswissenschaft, Universit\xE4t Z\xFCrich</p>\n<h3 id="erziehungsdirektoren"><a href="http://www.bildungsgeschichte.uzh.ch">Erziehungsdirektoren</a></h3>\n<p>Institut f\xFCr Erziehungswissenschaft Universit\xE4t Z\xFCrich</p>\n<h3 id="fondo-gianini-e-rara"><a href="https://www.e-rara.ch/fondo/nav/classification/10916052">Fondo Gianini (e-rara)</a></h3>\n<p>The platform for digitized rare books from Swiss libraries</p>\n<h3 id="fondo-gianini-supsi"><a href="http://fondo-gianini.supsi.ch/">Fondo Gianini (SUPSI)</a></h3>\n<p>Dipartimento formazione e apprendimento della Scuola universitaria professionale della Svizzera italiana (SUPSI DFA)</p>\n<h3 id="scriptura-paedagogica-online"><a href="http://goobiweb.bbf.dipf.de/viewer/browse/">Scriptura Paedagogica Online</a></h3>\n<p>Bibliothek f\xFCr Bildungsgeschichtliche Forschung Berlin (P\xE4dagogische Nachschlagewerke und P\xE4dagogische Zeitschriften) (8.2018)</p>\n<h3 id="stapfer-enqu\xEAte-1799"><a href="https://opendata.swiss/de/dataset/stapfer-enquete-schule-in-der-schweiz-1799">Stapfer-Enqu\xEAte 1799</a></h3>\n<p><a href="http://www.stapferenquete.ch/projekt">Project description</a></p>\n<p>Swiss Federal Archives</p>\n<h3 id="storiascuola"><a href="http://www2.supsi.ch/cms/storiascuola/">Storiascuola</a></h3>\n<p>Dipartimento formazione e apprendimento della Scuola universitaria professionale della Svizzera italiana (SUPSI DFA)</p>\n<h3 id="z\xFCrcher-klassenfotoarchiv"><a href="https://archives-quickaccess.ch/search/1">Z\xFCrcher Klassenfotoarchiv</a></h3>\n<p>State Archives of the canton of Zurich</p>\n<h3 id="z\xFCrcher-schulumfrage-177172"><a href="https://www.archives-quickaccess.ch/search/3">Z\xFCrcher Schulumfrage 1771/72</a></h3>\n<p>State Archives of the canton of Zurich</p>\n      '},Rights:{text:'\n<h2>Legal Notice</h2>\n<h3>Liability disclaimer</h3>\n<p>Although <em>History of Education Switzerland</em> has made every effort to ensure the accuracy of the information published here, it assumes no liability, express or implied, for the correctness, accuracy, reliability, or completeness of the information, nor does it guarantee the information is up-to-date.</p>\n<p><em>History of Education Switzerland</em> accepts no liability, whether direct or indirect, for material or non-material damages resulting from access and use, or non-use of the information published here, or resulting from misuse of the connection or technical malfunction.</p>\n<p><em>History of Education Switzerland</em> provides links to outside websites which are not held on its server, and over which it has no influence. <em>History of Education Switzerland</em> has not verified these sites, and is not responsible for the content or the services provided there. Should the contents of the linked websites provide reason for complaint, please inform <em>History of Education Switzerland</em>.</p>\n<h3>Terms of Use</h3>\n<p><em>History of Education Switzerland</em> holds no copyright over the knowledge portal contents. Generally, content rights are held by the catalogue owners.</p>\n<p>The documents herby published by <em>History of Education Switzerland</em> are free for non-commercial use in teaching and research, as well as for personal use.</p>\n<p>The publication of texts and images in print and online form is only permissible with the authorisation from their rights-holders. Written authorisation from the rights holders is also required for the systematic storage on other servers of parts, or the whole, of the electronic resources provided here.</p>\n      '},Tips:{text:'\n <h2>Tips &amp; Tricks</h2>\n <h3>Search for phrases</h3>\n <p>Search for co-occurring words as expressions: "H\xF6here T\xF6chterschule Z\xFCrich"<br /> (for this function, use quotes: \'\' )</p>\n <h3>Limiting your search</h3>\n <p>The search can be restricted to specific segments of a text</p>\n <ul>\n   <li>title:Pestalozzi (searches only the title of the text)</li>\n   <li>body:Pestalozzi (searches only the body of the text)</li>\n </ul>\n <p>The search can be restricted according to specific metadata</p>\n <ul>\n   <li>author:"Johann Heinrich Pestalozzi" (searches according to author) </li>\n   <li>publicationplace:Bern (searches according to place of publication)</li>\n   <li>publicationdate:1831 (searches according to publication date)</li>\n   <li>municipality:Aarau (searches according to municipality)</li>\n   <li>canton:Z\xFCrich (searches according to canton)</li>\n   <li>nation:Frankreich (searches according to country)</li>\n   <li>actors:Actors (searches according to actors)</li>\n   <li>texttype:Teaching material (searches according to type of text)</li>\n   <li>mediatype:Picture (searches according to type of media)</li>\n   <li>language:German (searches according to language)</li>\n   <li>project:Stapfer-Enqu\xEAte (searches according to inventory/project)</li>\n   <li>educationssystem:"baccalaureate schools" (searches according to education system)</li>\n   <li>subject:Childhood (searches according to subject)</li>\n </ul>\n <h3>Search Operators</h3>\n <p>By default, search terms are linked by the <em>or</em> function. This means that: <br /><em>Kindergarten municipality:Kilchberg</em><br /> searches for documents that contain either the term Kindergarten, or refer to the municipality of Kilchberg.</p>\n <p>When the + symbol is used before a term, only search results including that term will appear. When the - symbol is used before a term, it will be excluded from the search results.<br/> This is demonstrated by the following two examples:<br/> +Kindergarten\n   +Fr\xF6bel (all search results include both Kindergarten and Fr\xF6bel)<br/> +Kindergarten -Fr\xF6bel (Search results will include the term Kindergarten but not Fr\xF6bel).<p>\n <p>To perform a multiple character wildcard search use the * symbol.<p>\n <p>Pestal* (find Pestalozzi, Pestalozzianum, Pestalozzi-Schule, \u2026)<p>\n     '}});

