/* harmony default export */ __webpack_exports__["a"] = ({Global:{appTitle:'Histoire de l’Éducation Suisse',loading:'En train de charger...',open:'Ouvrir',more:'montrer plus',from:'De',until:'A',publicationDate:'Date de publication',referencePeriod:'Période de référence',tipsTricks:'Conseils & Astuces',noscript:"S'il vous plaît activer JavaScript pour rechercher.",poweredByText:'powered by Eurospider',eurospiderLink:'https://www.eurospider.com/en/'},Footer:{about:'Le projet',projects:'L’inventaire',rights:'Informations légales',copyright:'© 2018, Université de Zurich'},Home:{quickInfo:'Plateforme sur l’évolution historique de la formation et de l’éducation '},RecordList:{resetFacetSearch:'Répéter la recherche sans restrictions'},Records:{foundDocs:'documents trouvés'},SortDropdown:{asc:'Du plus ancien (date de publication)',desc:'Du plus récent (date de publication)',relevance:'Pertinence',sortBy:'Trier par'},About:{text:'\n<h2>Le projet</h2>\n<h3>Buts du projets</h3>\n<p>La plateforme <em>Histoire de l\u2019\xE9ducation suisse</em> permet aux chercheurs, \xE9tudiants, enseignants ainsi qu\u2019au public int\xE9ress\xE9 de mener des investigations disciplinaires et multilingues sur les sources et donn\xE9es issues de la recherche historique en \xE9ducation. Les investigations se font via un moteur de recherche et permettent l\u2019acc\xE8s \xE0 une importante infrastructure digitale consacr\xE9e \xE0 l\u2019histoire de l\u2019\xE9ducation en Suisse.</p>\n<p>Les chercheur.e.s ont la possibilit\xE9 de publier des donn\xE9es de recherche historiques sur l\'\xE9ducation conform\xE9ment \xE0 la strat\xE9gie du FNS sur le libre-acc\xE8s aux donn\xE9es de la recherche \u2013 (Open Research Data).</p>\n<h3 id="organisation-du-projet">Organisation du projet</h3>\n<h4 id="suivi-du-projet">Suivi du projet</h4>\n<p>Groupe de travail Recherche en histoire de l\u2019\xE9ducation (SSRE)</p>\n<h4 id="direction-du-projet-d\xE9veloppement-technique">Direction du projet (d\xE9veloppement technique)</h4>\n<p>Christina Rothen; Thomas Ruoss; Lucien Criblez (Universit\xE9 de Z\xFCrich)<br />\nDirection du projet</p>\n<h4 id="equipe-de-base-du-projet-d\xE9veloppement-technique">Equipe de base du projet (d\xE9veloppement technique) </h4>\n<p>Peter Sch\xE4uble (<a href="https://www.eurospider.com/en/">Eurospider Information Technology AG)</a><br />\nD\xE9veloppement du moteur de recherche</p>\n<p>Amanda Sauter &amp; Mauro Bieg (2324.ch)<br />\nDesign et d\xE9veloppement web frontal</p>\n<p>Sergio Maffioletti (S3IT, Universit\xE9 de Z\xFCrich)<br />\nBackend et repository (Coordination DaSCH)</p>\n<p>Carmen Flury, Tamara Lehner-Loosli, Lars Heinzer (Universit\xE9 de Z\xFCrich)<br />\nCollaborteurs du projet Bildung in Zahlen</p>\n<p>Nous remercions les collaboratrices et collaborateurs du Dipartimento formazione e apprendimento della Scuola universitaria professionale della Svizzera italiana (SUPSI DFA), de la Bibliothek f\xFCr Bildungsgeschichtliche Forschung des DIPF (BBF), des Archives f\xE9d\xE9rales suisses, des Archives d\'\xC9tat de Zurich, de la Biblioth\xE8que centrale de Zurich, de l\' ETH-Bibliothek et de la Fondation Pestalozzianum. Nous remercions notamment Wolfgang Saalfeld, Flavian Imlig, Lars M\xFCller, Giorgia Masoni, Anja Giudici, Alexandre Fontaine, Karin Manz, Caroline Suter et Simon Allemann pour leur soutien.</p>\n<h3>Contact</h3>\n<p><a href="mailto:redaktion@bildungsgeschichte.ch">redaction@histoiredeleducation.ch</a></p>\n      '},Projects:{text:'\n<h2 id="les-collections">Les collections</h2>\n<p>(Statut 26.06.2018)</p>\n<h3 id="bildung-in-zahlen"><a href="http://www.bildungsgeschichte.uzh.ch">Bildung in Zahlen</a></h3>\n<p>Institut des sciences de l\'\xE9ducation, Universit\xE9 de Z\xFCrich</p>\n<h3 id="erziehungsdirektoren"><a href="http://www.bildungsgeschichte.uzh.ch">Erziehungsdirektoren</a></h3>\n<p>Institut des sciences de l\'\xE9ducation, Universit\xE9 de Z\xFCrich</p>\n<h3 id="fondo-gianini-e-rara"><a href="https://www.e-rara.ch/fondo/nav/classification/10916052">Fondo Gianini (e-rara)</a></h3>\n<p>Le portail pour les imprim\xE9s num\xE9ris\xE9s des biblioth\xE8ques suisses</p>\n<h3 id="fondo-gianini-supsi"><a href="http://fondo-gianini.supsi.ch/">Fondo Gianini (SUPSI)</a></h3>\n<p>Dipartimento formazione e apprendimento della Scuola universitaria professionale della Svizzera italiana (SUPSI DFA)</p>\n<h3 id="scriptura-paedagogica-online"><a href="http://goobiweb.bbf.dipf.de/viewer/browse/">Scriptura Paedagogica Online</a></h3>\n<p>Bibliothek f\xFCr Bildungsgeschichtliche Forschung Berlin (P\xE4dagogische Nachschlagewerke und P\xE4dagogische Zeitschriften) (8.2018)</p>\n<h3 id="stapfer-enqu\xEAte-1799"><a href="https://opendata.swiss/de/dataset/stapfer-enquete-schule-in-der-schweiz-1799">Stapfer-Enqu\xEAte 1799</a></h3>\n<p><a href="http://www.stapferenquete.ch/projekt">Description du projet</a></p>\n<p>Archives f\xE9d\xE9rales suisses</p>\n<h3 id="storiascuola"><a href="http://www2.supsi.ch/cms/storiascuola/">Storiascuola</a></h3>\n<p>Dipartimento formazione e apprendimento della Scuola universitaria professionale della Svizzera italiana (SUPSI DFA)</p>\n<h3 id="z\xFCrcher-klassenfotoarchiv"><a href="https://archives-quickaccess.ch/search/1">Z\xFCrcher Klassenfotoarchiv</a></h3>\n<p>Archives d\u2019Etat du canton de Zurich</p>\n<h3 id="z\xFCrcher-schulumfrage-177172"><a href="https://www.archives-quickaccess.ch/search/3">Z\xFCrcher Schulumfrage 1771/72</a></h3>\n<p>Archives d\u2019Etat du canton de Zurich</p>\n      '},Rights:{text:'\n<h2>Mentions l\xE9gales</h2>\n<h3>Avertissement de responsabilit\xE9</h3>\n<p>Malgr\xE9 la grande attention qu\u2019elle porte \xE0 la justesse des informations diffus\xE9es <em>Histoire de l\u2019\xE9ducation suisse</em> ne peut \xEAtre tenue responsable quant \xE0 la fid\xE9lit\xE9, l\u2019exactitude, l\u2019actualit\xE9, la fiabilit\xE9 et l\u2019int\xE9gralit\xE9 de ces informations.</p>\n<p><em>Histoire de l\u2019\xE9ducation suisse</em> ne saurait \xEAtre tenue responsable des dommages mat\xE9riels ou immat\xE9riels qui pourraient \xEAtre caus\xE9s par l\u2019acc\xE8s aux informations diffus\xE9es ou par leur utilisation ou non-utilisation, le mauvais usage de la connexion ou par des probl\xE8mes techniques.</p>\n<p><em>Histoire de l\u2019\xE9ducation suisse</em> n\'a pas v\xE9rifi\xE9 les sites tiers, c\'est-\xE0-dire les sites qui ne sont pas li\xE9s \xE0 ce site via des liens, et n\'assume aucune responsabilit\xE9 pour le contenu ou les services offerts. Si le contenu des pages li\xE9es fait l\'objet d\'une plainte, <em>Histoire suisse de l\u2019\xE9ducation</em> demande une communication.</p>\n<h3>Conditions d\'utilisation</h3>\n<p><em>Histoire de l\u2019\xE9ducation suisse</em> n\'a aucun droit d\'auteur sur le contenu du portail de connaissances. Les droits sont g\xE9n\xE9ralement avec les propri\xE9taires.</p>\n<p>Les documents publi\xE9s sur la plateforme <em>Histoire de l\u2019\xE9ducation suisse</em> sont accessibles gratuitement pour un usage non commercial, ou \xE0 des fins d\'enseignement et de recherche.</p>\n<p>Des textes et des images ne peuvent \xEAtre publi\xE9es dans des publications imprim\xE9es ou en ligne que sur autorisation pr\xE9alable du d\xE9tenteur des droits d\'auteur. La copie syst\xE9matique des parties de l\'offre \xE9lectronique sur d\'autres serveurs n\xE9cessite \xE9galement le consentement \xE9crit du titulaire des droits d\'auteur.</p>\n      '},Tips:{text:'\n    <h2>Conseils &amp; Acustes </h2>\n      <h3>Recherche par expressions</h3>\n      <p>Recherche des expressions connexes: "\xC9cole des filles de Fribourg"<br />\n      (Utilisez \xE0 cet \xE9gard les symboles de double codage " " )</p>\n      <h3>Restrictions</h3>\n      <p>Rechercher des \xE9l\xE9ments sp\xE9cifiques du texte</p>\n      <ul>\n        <li>title:"Didactica magna" (Recherche par titre du document)</li>\n        <li>body:Didactica (Recherche dans le corps du texte) </li>\n      </ul>\n      <p>Restreindre la recherche via m\xE9tadonn\xE9es</p>\n      <ul>\n        <li>author:"Gr\xE9goire Girard" (Recherche par auteur) </li>\n        <li>publicationplace:Neuch\xE2tel (Recherche par lieu)</li>\n        <li>publicationdate:1831 (Recherche par date de publication)</li>\n        <li>municipality:Bienne (Recherche par commune)</li>\n        <li>canton:Z\xFCrich (Recherche par canton)</li>\n        <li>nation:Suisse (Recherche par pays)</li>\n        <li>actors:Enseignants (Recherche par acteurs)</li>\n        <li>texttype:Actes (Recherche par type de textes)</li>\n        <li>mediatype:Photographie (Recherche par types de media)</li>\n        <li>language:Italien (Recherche par langues)</li>\n        <li>project:Stapfer-Enqu\xEAte (Recherche par inventaire/project)</li>\n        <li>educationssystem:"\xE9cole \xE9l\xE9mentaire" (Recherche par syst\xE8me de formation)</li>\n        <li>subject:Enfance (Recherche par th\xE8me de r\xE9f\xE9rence)</li>\n      </ul>\n      <h3>Op\xE9rateurs de recherche</h3>\n      <p>L\u2019op\xE9rateur <em>ou</em> est l\u2019op\xE9rateur par d\xE9faut. L\u2019espace entre deux termes est donc \xE9quivalent \xE0 l\u2019op\xE9rateur <em>ou</em>, c\u2019est-\xE0-dire que en cherchant <br /><em>"Jardin d\'enfants" municipality:Lausanne</em><br /> vous obtiendrez les documents qui concernent soit les jardin d\u2019enfants soit la commune de Lausanne.<br />\n      <p>Le signe + signifie que le terme qui suit est obligatoire. Le signe - exclu le terme qui suit de la recherche.<br />\n      Les deux exemples suivants servent \xE0 illustrer la marche \xE0 suivre:<br/>\n      +Mutuel +Girard (Les r\xE9sultats de la recherche incluront n\xE9cessairement les deux termes Mutuel et Girard) <br />\n      +Mutuel -Girard (Les r\xE9sultats de la recherche incluront le terme Mutuel mais pas le terme Girard)<p>\n      <p>Afin de laisser un nombre ind\xE9fini de caract\xE8res libres \xE0 la fin d\u2019un terme de recherche, utilisez * <br />\n      Pestal* (Les r\xE9sultats de recherches comprendront Pestalozzi, Pestalozzianum, Pestalozzien, etc.)<p> '}});

