/* harmony default export */ __webpack_exports__["a"] = ({Global:{appTitle:'Storia dell’Educazione Svizzera',loading:'Sta caricando...',open:'Aprire',more:'mostrare di più',from:'Dal',until:'Al',publicationDate:'Data di pubblicazione',referencePeriod:'Periodo di riferimento',tipsTricks:'Consigli & trucchi',noscript:'Si prega di attivare JavaScript per la ricerca.',poweredByText:'powered by Eurospider',eurospiderLink:'https://www.eurospider.com/en/'},Footer:{about:'Il progetto',projects:'La continuità',rights:'Informazioni legali',copyright:'© 2018, Università di Zurigo'},Home:{quickInfo:"Portale sulla storia dell’educazione e della formazione"},RecordList:{resetFacetSearch:'Ripetere la ricerca senza restrizioni'},Records:{foundDocs:'documenti trovati'},SortDropdown:{asc:'Dal più vecchio (data di pubblicazione)',desc:'Dal più recente (data di pubblicazione)',relevance:'Rilevanza',sortBy:'Ordina per'},About:{text:'\n    <h2>Il Progetto</h2>\n    <h3>Obiettivi del progetto</h3>\n    <p>Il portale dei <em>Storia dell\u2019Educazione Svizzera</em> consente ai ricercatori e alle ricercatrice, agli e alle studenti, agli studiosi e alle studiose e ad un pubblico interessato di effettuare una ricerca disciplinare strutturata come pure multilingue sulle fonti e sui dati relativi alla ricerca in storia dell\u2019Educazione per la Svizzera. Il potenziale informativo della storia dell\u2019educazione delle diverse infrastrutture digitali pu\xF2 essere trovato tramite una dei parametri di ricerca. <br /> Ai ricercatori \xE8 offerta una semplice opportunit\xE0 di pubblicare i dati risultati dalla ricerca storica nel campo dell\u2019educazione nel senso della gestione dei dati di ricerca (Strategia FNS per i Open Research Data).</p>\n    <h3>Organizzazione del progetto</h3>\n    <h4>Gruppo disciplinare</h4>\n    <p>Gruppo di lavoro storia dell\u2019educazione (SSRE)</p>\n    <h4 id="team-del-progetto-fase-di-sviluppo-tecnico">Team del progetto (fase di sviluppo tecnico)</h4>\n    <p> Christina Rothen; Thomas Ruoss; Lucien Criblez  (Universit\xE0 di Zurigo)<br />\n    Gestione del progetto</p>\n    <p>Peter Sch\xE4uble (<a href="https://www.eurospider.com/en/">Eurospider Information Technology AG)</a><br />\n    Sviluppo del motore di ricerca</p>\n    <p>Amanda Sauter &amp; Mauro Bieg (2324.ch)<br />\n    Design e sviluppo dell\u2019interfaccia</p>\n    <p>Sergio Maffioletti (S3IT, Universit\xE0 di Zurigo)<br />\n    Backend e repository a lungo termine (Coordinazione DaSCH)</p>\n    <p>Carmen Flury, Tamara Lehner-Loosli, Lars Heinzer (Universit\xE0 di Zurigo)<br />\n    Collaboratore e collaboratrici Bildung in Zahlen</p>\n    <p>Ringraziamo le collaboratrici e i collaboratori del Dipartimento formazione e apprendimento della Scuola universitaria professionale della Svizzera italiana (SUPSI DFA), della Bibliothek f\xFCr Bildungsgeschichtliche Forschung al Deutsches Institut f\xFCr Internationale P\xE4dagogische Forschung (BBF), dell\u2019Archivio federale svizzero, dello archivio di stato del cantone Zurigo, della Zentralbibliothek Z\xFCrich, della ETH-Bibliothek e della Stiftung Pestalozzianum. Ringraziamo in particolare Flavian Imlig, Wolfgang Saalfeld, Lars M\xFCller, Anja Giudici, Giorgia Masoni, Alexandre Fontaine, Karin Manz, Caroline Suter e Simon Allemann per il loro prezioso aiuto.</p>\n    <h3>Contatti</h3>\n    <p><a href="mailto:redazione@storiadelleducazione.ch">redazione@storiadelleducazione.ch</a></p>\n      '},Projects:{text:'\n<h2 id="le-collezioni">Le collezioni</h2>\n<p>(Stato 26.06.2018)</p>\n<h3 id="bildung-in-zahlen"><a href="http://www.bildungsgeschichte.uzh.ch">Bildung in Zahlen</a></h3>\n<p>Institut f\xFCr Erziehungswissenschaft, Universit\xE4t Z\xFCrich</p>\n<h3 id="erziehungsdirektoren"><a href="http://www.bildungsgeschichte.uzh.ch">Erziehungsdirektoren</a></h3>\n<p>Institut f\xFCr Erziehungswissenschaft, Universit\xE4t Z\xFCrich</p>\n<h3 id="fondo-gianini-e-rara"><a href="https://www.e-rara.ch/fondo/nav/classification/10916052">Fondo Gianini (e-rara)</a></h3>\n<p>La piattaforma per la consultazione di edizioni antiche digitalizzate conservate nelle biblioteche svizzere</p>\n<h3 id="fondo-gianini-supsi"><a href="http://fondo-gianini.supsi.ch/">Fondo Gianini (SUPSI)</a></h3>\n<p>Dipartimento formazione e apprendimento della Scuola universitaria professionale della Svizzera italiana (SUPSI DFA)</p>\n<h3 id="scriptura-paedagogica-online"><a href="http://goobiweb.bbf.dipf.de/viewer/browse/">Scriptura Paedagogica Online</a></h3>\n<p>Bibliothek f\xFCr Bildungsgeschichtliche Forschung Berlin (P\xE4dagogische Nachschlagewerke und P\xE4dagogische Zeitschriften) (8.2018)</p>\n<h3 id="stapfer-enqu\xEAte-1799"><a href="https://opendata.swiss/de/dataset/stapfer-enquete-schule-in-der-schweiz-1799">Stapfer-Enqu\xEAte 1799</a></h3>\n<p><a href="http://www.stapferenquete.ch/projekt">Descrizione del progetto</a></p>\n<p>Archivio federale svizzero</p>\n<h3 id="storiascuola"><a href="http://www2.supsi.ch/cms/storiascuola/">Storiascuola</a></h3>\n<p>Dipartimento formazione e apprendimento della Scuola universitaria professionale della Svizzera italiana (SUPSI DFA)</p>\n<h3 id="z\xFCrcher-klassenfotoarchiv"><a href="https://archives-quickaccess.ch/search/1">Z\xFCrcher Klassenfotoarchiv</a></h3>\n<p>Archivio di stato del cantone Zurigo</p>\n<h3 id="z\xFCrcher-schulumfrage-177172"><a href="https://www.archives-quickaccess.ch/search/3">Z\xFCrcher Schulumfrage 1771/72</a></h3>\n<p>Archivio di stato del cantone Zurigo</p>\n      '},Rights:{text:'\n<h2>Note legali</h2>\n<h3>Esclusione delle responsabilit\xE0</h3>\n<p>Sebbene <em>Storia dell\u2019Educazione Svizzera</em> presti molta attenzione all\u2019esattezza delle informazioni pubblicate, per quanto riguarda l\u2019esattezza dei contenuti, l\u2019esattezza, l\u2019attualit\xE0, l\u2019affidabilit\xE0 e la completezza di queste informazioni, non pu\xF2 essere fornita alcuna garanzia.</p>\n<p>Diritti di responsabilit\xE0 contro <em>Storia dell\u2019Educazione Svizzera</em> a causa di danni di natura materiale o immateriale derivanti, per esempio, dall\u2019accesso, dall\u2019uso o dal mancato utilizzo delle informazioni pubblicate, dall\u2019abuso del collegamento o da guasti tecnici sono esclusi.</p>\n<p><em>Storia dell\u2019Educazione Svizzera</em> non verifica i siti web di terzi, ci\xF2 significa che per i siti web che non si trovano sui loro server o nella loro sfera di influenza e che sono collegati a questo sito web tramite un link, non si assume alcuna responsabilit\xE0 per ci\xF2 che concerne il contenuto o i servizi offerti. Nel caso in cui il contenuto delle pagine collegate al sito web dia luogo a una denuncia, Storia dell\u2019Educazione Svizzera prega di notificarlo.</p>\n<h3>Condizioni di uso</h3>\n<p><em>Storia dell\u2019Educazione Svizzera</em> non ha copyright rispetto al contenuto del portale. I diritti, di norma, sono declinati ai proprietari.</p>\n<p>I documenti pubblicati in <em>Storia dell\u2019Educazione Svizzera</em> sono messi a disposizione degli utenti per scopi non commerciali nel campo dell\u2019insegnamento e della ricerca, come pure per l\u2019uso privato.</p>\n<p>La pubblicazione di testi e immagini in pubblicazioni stampate e in pubblicazioni online \xE8 consentita solo previa autorizzazione del detentore del copyright. L\'archiviazione sistematica, completa o parziale, dell\'offerta elettronica su altri server richiede anche il consenso scritto dei titolari del copyright.</p>\n      '},Tips:{text:'\n      <h2>Consigli &amp; trucchi</h2>\n      <h3>Ricerca per espressioni</h3>\n      <p>Ricerca di termini correlati: "Scuola media"<br /> (Utilizzare virgolette alte doppie)</p>\n      <h3>Restrizioni coercitive</h3>\n      <p>Restringere la ricerca a determinante parti del documento</p>\n      <ul>\n        <li>title:"Didactica magna" (Ricerca per titolo)</li>\n        <li>body:Pestalozzi (Cercare nel testo integrale</li>\n      </ul>\n      <p>Restringere la ricerca attraverso i metadati</p>\n      <ul>\n        <li>author:"Johann Heinrich Pestalozzi" (Ricerca per autore) </li>\n        <li>publicationplace:Bern (Ricerca per luogo di pubblicazione)</li>\n        <li>publicationdate:1831 (Ricerca per data di pubblicazione)</li>\n        <li>municipality:Giubiasco (Ricerca per comune)</li>\n        <li>canton:Ticino (Ricerca per cantone)</li>\n        <li>nation:Frankreich (Ricerca per paese)</li>\n        <li>actors:Insegnanti (Ricerca per attori)</li>\n        <li>texttype:Materiale didattico (Ricerca per tipo di testo)</li>\n        <li>mediatype:Testo (Ricerca per tipo di supporto)</li>\n        <li>language:tedesco (Ricerca di lingua)</li>\n        <li>project:Stapfer-Enqu\xEAte (Ricerca di inventario/progetto)</li>\n        <li>educationssystem:Scuola superiore (Ricerca per sistema di educazione)</li>\n        <li>subject:Giovane (Ricerca per tema di riferimento)</li>\n      </ul>\n      <h3>Operatori di ricerca</h3>\n      <p>Come impostazione predefinita i termini di ricerca sono collegati con <em>or</em>, quindi <em>asilo municipality:Lugano</em> includer\xE0 nella ricerca documenti nei quali appaiono o il termine asilo o la citt\xE0 di Lugano.</p>\n      <p>Usando l\u2019operatore + si includono nella ricerca solo documenti che contengono il termine che figura dopo questo simbolo. Usando l\u2019operatore - si escludono dalla ricerca documenti che contengono il termine che figura dopo questo simbolo. <br /> I due esempi\n        seguenti illustrano l\u2019uso corretto di questi due operatori:<br />\n        +asili +Fr\xF6bel (I risultati della ricerca includeranno documenti che menzionano entrambi i termini asilo e Fr\xF6bel)<br />\n         +Asili -Fr\xF6bel (I risultati della ricerca includeranno documenti che menzionano il termine asilo ma non si riferiscono a Fr\xF6bel)<p>\n      <p> Per lasciare un numero di caratteri indefinito alla fine della ricerca \xE8 possibile usare il carattere * </br>\n      Pestal* (i risultati della ricerca sono Pestalozzi, Pestalozzianum, Scuola-Pestalozzi, etc) <p> '}});

